import Vue from 'vue';
import Router from 'vue-router';

const Hello = () => import(/* webpackChunkName: "apply" */ '@/views/Index');
const Story = () => import(/* webpackChunkName: "error" */ '@/views/home');
const Birthday = () => import(/* webpackChunkName: "error" */ '@/views/mobile-dialog/index');

Vue.use(Router);

const routes = [
  {
    path: '/',
    name: 'hello',
    component: Hello,
    meta: {
      title: '我们的开始'
    }
  },
  {
    path: '/Story',
    name: 'Story',
    component: Story,
    meta: {
      title: '我们的故事'
    }
  },
  {
    path: '/birthday',
    name: 'birthday',
    component: Birthday,
    meta: {
      title: '我们的故事'
    }
  },
];

const router = new Router({
  // mode: 'history',
  // mode: 'hash',
  routes
})

export default router;
